body {
  width: 100%;
  height: 100%;
}


ul, li {
  text-decoration: none;
  list-style:none;
}

li {
  margin-bottom: .3rem;
}

h1 {
  color: rgba(0, 0, 0, 0.733);
  margin-left: .5rem;
}

.Navbar {
  width: 100%;
  height:auto;
  position: fixed;
  background-color: white;
}

.app {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  padding-top: 10rem;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: rgb(255, 253, 253); 
  border-radius: 10px;
}

.container {
  width: 80%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  grid-template-rows: 300px 300px;
  grid-gap: 4rem;
  align-items: center;
  justify-items: center;
  overflow-x: hidden;
  padding: 1rem;
}

.card {
  width: 250px;
  height: 259px;
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 10px rgba(129, 178, 154, 0.28);
  border-radius: 5px;
  padding: 1rem;
}

.items-container {
  display: flex;
  flex-direction: column;
  gap:.4rem;
}